import {mapState} from 'vuex';
import {get,post} from '@/common/methods.js';
import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue";
import edit from '@/components/wjx-edit/wjx-edit.vue';
export default {
	components: {
		uniSegmentedControl,
		edit
	},

	onNavigationBarButtonTap: function(e) {
		// var that = this;  
		// 子组件的data
		// var editData = this.$refs.edit.submit(); 
		 // this.editData = this.$refs.edit.submit(); 
	//	console.log(editData)
		
		// console.log("内容为" + JSON.stringify(this.editData.editItems));
		
		var that = this;  
		//子组件的data
		var editData = this.$refs.edit.submit(); 
		
		console.log("内容为" + JSON.stringify(editData.editItems));
	},
	data() {
		return {
			editData:'',
			title: 'Hello',
			enteroriseList: [],
			isVip:'',
			bookArray: ['上传时间','阅读量'],
			journaArray: ['上架时间','阅读量'],
			courseArray: ['上架时间','阅读量'],
			bookIndex: 0,
			journaIndex: 0,
			courseIndex: 0,
			bookList: [],
			journalList: [],
			items: ['主页', '图书', '杂志', '课程'],
			current: 0,
			requiredBooks: {
				"page_index": 1,
				"page_size": 10,
				"table_id": "",
				"tp": "2",
				"sort": "createtime"
			},
			requiredJournal: {
				"page_index": 1,
				"page_size": 10,
				"table_id": "",
				"tp": "2",
				"resource_type": 2,
				"sort": "createtime"
			}
		}
	},
	computed: {
		...mapState([
			"userid",
			"returnData"
		]),
	},
	onLoad(e) {
	
		uni.setNavigationBarTitle({
			title:''
		});
		this.requiredBooks.table_id = this.requiredJournal.table_id = e.enterpriseid
		this.isVip = e.joinedState;
		if (this.isVip == 'init') {
			uni.showModal({
				title: '提示',
				showCancel: false,
				confirmText: '关闭',
				content: '您已申请企业会员，请等待审核。',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击关闭');
					}
				}
			});
		} else if (this.isVip == 'notVip') {
			uni.showModal({
				title: '提示',
				confirmText: '免费申请',
				cancelText: '我先看看',
				content: '为了更好的享受企业服务，请先申请为企业会员',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: './applyMember'
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		} else if (this.isVip == 'pass') {
			console.log('hello vip')
		}
		get('/enterprise/company/'+this.requiredBooks.table_id,{'userId' : this.userid}).then(res=>{
			console.log(res.data);
			this.enteroriseList = res.data;
		})

	},
	
	methods: {
		call() {
			console.log("d方法")
					var editData = this.$refs.edit.submit(); 
					
					var a= JSON.stringify(editData .editItems)
					console.log("内容为" + a);
					uni.showToast({
						title: a,
						duration: 2000,
						icon: 'none',
					});
		
		},
		bookSearchChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.bookIndex = e.target.value
			if(e.target.value =='1' || e.target.value == 1){
				this.requiredBooks.sort = 'hit'
			}else if(e.target.value =='0' || e.target.value == 0){
				this.requiredBooks.sort = 'createtime'
			}
			post('/book/book/page',this.requiredBooks).then(res=>{
				console.log(res);
			},err=>{
				//异步错误处理
			});	
			
		},
		journaSearchChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.journaIndex = e.target.value
			if(e.target.value =='1' || e.target.value == 1){
				this.requiredJournal.sort = 'hit'
			}else if(e.target.value =='0' || e.target.value == 0){
				this.requiredJournal.sort = 'createtime'
			}
			post('/book/book/page',this.requiredJournal).then(res=>{
				console.log(res);
				
			},err=>{
				//异步错误处理
			});	
		},
		courseSearchChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.courseIndex = e.target.value
		},
		onClickItem(index) {
			//console.log(index)
			if (this.current !== index) {
				this.current = index;
			}
			if (index == 1) {
				console.log('111')
				uni.request({
					url: 'http://192.168.0.210:9999/book/book/page',
					method: 'POST',
					data: this.requiredBooks,
					header: {
						'content-type': 'application/json'
					},
					success: res => {
						console.log(res)
						this.bookList = res.data.data.pageBooks;
					},

				});
			}else if(index == 2){
					console.log('222')
					uni.request({
						url: 'http://192.168.0.210:9999/book/book/page',
						method: 'POST',
						data: this.requiredJournal,
						header: {
							'content-type': 'application/json'
						},
						success: res => {
							console.log(res.data.data.pageBooks);
							this.journalList = res.data.data.pageBooks;
							
						},
					
					});
			}
		},
		
		toEnterprise() {
			uni.reLaunch({
				url: '../index/enterprise/enterprise'
			});
		},
		
		to_bookDetails(e) {
			console.log(e);
			let	bookId = e.currentTarget.dataset.id;
			uni.navigateTo({
				
			 url: '../books/bookDetails?id='+bookId
			})
		},
		
	}
}
